@import '../../../../../styles/mixins/mixins';

.team-channel-header__container {
  @include workspace-header-layout;
  @include workspace-header-theme;
}

.team-channel-header__name {
  @include ellipsis-text;
}

.team-channel-header__name.user {
  font: var(--font-weight-medium);
  font-size: 14px;
}

.workspace-pin-button {
  width: 20px;
  height: 20px;

  background: url('../../../../../assets/icons/pinIcon.svg') no-repeat;
  border-radius: 30px;
}

.workspace-info-button {
  width: 20px;
  height: 20px;

  background: url('../../../../../assets/icons/infoIcon.svg') 1px no-repeat;
  border-radius: 30px;
}
