@import '../../../../styles/mixins/mixins';

.channel-search__container {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 10px 10px 8px;

  border-top: 1px solid #0003;
}

.channel-search__input__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;

  min-width: 0;
  height: 40px;
  padding-left: 32px;

  background: rgb(255 255 255 / 20%) url('../../../../assets/icons/search.svg') 10px 50% no-repeat;
  border: 1px solid transparent;
  border-radius: 30px;

  &:focus-within {
    border: 1px solid var(--static-background-color);
  }

  input::placeholder {
    color: rgb(255 255 255 / 66%);
  }

  input {
    min-width: 0;

    font-size: 16px;
    color: var(--static-background-color);

    background: none;
    border: none;
    outline: none;
  }
}

.channel-search__results {
  position: absolute;
  z-index: 10;
  top: 16px;
  left: 230px;

  width: 300px;
  height: fit-content;

  background: white;
  border: 1px solid var(--panel-secondary-background-color);
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 6%);

  .channel-search__results-header {
    display: flex;
    align-items: center;

    width: fit-content;
    margin-left: 12px;

    font-size: 14px;
    font-weight: var(--font-weight-medium);
    line-height: 120%;
    color: var(--text-low-emphasis-color);

    i {
      margin-left: 12px;
    }
  }

  .channel-search__result-container {
    cursor: pointer;

    display: flex;
    gap: 8px;
    align-items: center;

    width: 100%;
    height: 48px;
    padding: 0 12px;

    &.highlighted,
    &:hover {
      background: var(--primary-color-alpha);
    }

    .result-hashtag {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 28px;
      height: 24px;

      font-size: 14px;
      font-weight: var(--font-weight-bold);
      line-height: 120%;
      color: white;

      background: var(--primary-color);
      border-radius: 24px;
    }

    .channel-search__result-text {
      @include ellipsis-text;

      width: 100%;

      font-size: 14px;
      font-weight: var(--font-weight-medium);
      line-height: 120%;
      color: var(--text-color);
    }
  }
}
