.channel-empty__container {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  height: 100%;
  padding: 20px;

  .channel-empty__avatars {
    display: flex;
  }

  .channel-empty__avatars div:first-child {
    z-index: 3;
  }

  .channel-empty__avatars div:nth-child(2) {
    position: relative;
    z-index: 2;
    right: 32px;
  }

  .channel-empty__avatars div:nth-child(3) {
    position: relative;
    z-index: 1;
    right: 64px;
  }

  .channel-empty__avatars .str-chat__avatar {
    margin-right: 0;
  }

  .channel-empty__main-description {
    margin-bottom: 10px;

    font-size: 18px;
    font-weight: var(--font-weight-bold);
    line-height: 120%;
    color: var(--text-color);
  }

  .channel-empty__secondary-description {
    margin: 0;

    font-size: 14px;
    line-height: 120%;
    color: var(--text-low-emphasis-color);
  }

  .channel-empty__main-description .channel-empty__user-name {
    color: var(--primary-color);
  }
}
